@media only screen and (max-width: 320px) {
    .project {
      width: 90vw;
    }
  
    .image-div img {
      width: 90vw;
      height: auto;
    }
  
    .overlay {
      height: 155px !important;
    }
  }
#background-img{
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    max-width: 350px;
}
#overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 200px;
    opacity: 0;
    transition: 0.5s ease;
    background-color: black;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;

}

#project:hover #overlay{
    opacity: 0.7;
    height: 200px;
    background-color:black;
    
}
#project:hover{
    transform: scale(1.1);
    transition: 0.7s;
}
#project-container{
    display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 70vw;
}
#project{
    margin: 10px;
    border-radius: 5px;
    position: relative;
    box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6);
}
#project-details{
    color: white;
    font-weight: bold;
    padding: 0 10px;
    text-align: justify;
}
#tech-div{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 5px 0 10px;
    flex-grow: 1;
    padding-bottom: 50px;
}
#project-link{
    display: flex;
  justify-content: center;
  align-items: center;
  background: black;
  width: 95%;
  padding: 10px;
  color: white;
  position: absolute;
  bottom: 0;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
}


